.done {
    position: fixed;
    top: 24px;
    left: 50%;
    z-index: 100;
    transform: translate(-50%, -50%) scale(0);
    max-width: 90vw;
    width: 430px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;
    background-color: $color-white;
    border: 1px solid $color-gray-light-3;
    box-shadow: 0 14px 40px rgba(165, 165, 197, 0.16);
    border-radius: 6px;

    transition: transform 0.3s;

    svg {
        margin-right: 16px;
    }

    &__text {
        font-weight: 500;
        font-size: 17px;
        line-height: 24px;
    }

    &.active {
        transform: translate(-50%, 0) scale(1);
    }
}
